import * as types from '../../actions/action_types';
import updateObject from '../reducer.utils';

export const initialState = {
    merchants: [],
    isLoading: false,
    response: null,
    success: null,
    errorMessage: null,
}
const updateCreateMerchantSuccessState = (state, action) => {
   
  return {
    ...state,
    isLoading: false,
    response: action.payload,
    success: true,
  };
};

const updateCreateSuperUserSuccessState = (state, action) => {
  return {
    ...state,
    isLoading: false,
    response: action.payload,
    success: true,
  };
};

const updateCreateSuperUserFailedState = (state, action) => {
  return {
    ...state,
    isLoading: false,
    response: action.payload,
    success: false,
  };
};
const updateCreateMerchantFailedState = (state, action) =>
  updateObject(state, {
    success: false,
    isLoading: false,
    response: action.payload,
    errorMessage: action.payload.response.data.message,
  });

const updateGetAllMerchantsSuccessState = (state, action) => {
    return {
      ...state,
      merchants: [...action.payload],
      isLoading: false,
      response: action.payload,
      success: true,
    };
};

const updateGetAllMerchantsFailedState = (state, action) => {
  return {
    ...state,
    success: false,
    isLoading: false,
    getAllStoresError: true,
    response: action.payload,
    errorMessage: action.payload.response.data.message,
  };
};

const StartRequest = (state) => {
    return {
        ...state,
        isLoading: true,
        errorMessage: null,
        success: false


    }
}
const merchantReducer = (state = initialState, action) => {
    switch (action.type) {
      case types.API_REQUEST:
        return StartRequest(state);
      case types.GET_ALL_MERCHANTS_FAILED:
        return updateGetAllMerchantsFailedState(state, action);
      case types.GET_ALL_MERCHANTS_SUCCESS:
        return updateGetAllMerchantsSuccessState(state, action);
      case types.CREATE_MERCHANT_SUCCESS:
        return updateCreateMerchantSuccessState(state, action);
      case types.CREATE_MERCHANT_FAILED:
        return updateCreateMerchantFailedState(state, action);
      case types.CREATE_SUPER_USER_SUCCESS:
        return updateCreateSuperUserSuccessState(state, action)
      case types.CREATE_SUPER_USER_FAILED:
        return updateCreateSuperUserFailedState(state, action);
      default:
        return state;
    }
};

export default merchantReducer;

